.container-page-margin {
    width: calc(100vw - 56px);
    // max-width: 1825px;
    height: calc(100vh - 36px);
    // margin: 0 auto;
    position: relative;
    max-height: 969px;
    max-width: 1825px;
    margin: 20px auto;
    // transition: .3s;

    @media (max-width: 1000px) {
        max-height: unset;
        width: calc(100% - 50px);
        height: unset;

    }
}



.footer-container {
    padding: 58px 156px 147px;
    width: 100%;
    position: relative;
    // padding-top: 58px;
    // padding-bottom: 147px;

}

.container-page {
    width: 100vw;
    height: 100vh;
    // position: relative;
    @media(max-width: 1000px){
        height: unset;
    }
}

.container-content {
    padding: 30px 100px;
    position: relative;
    width: 100%;
    height: 100%;
    // overflow: hidden;

    @media (max-width: 1439px) {
        padding: 16px 75px;
    }

    @media (max-width: 1023px) {
        padding: 11px 53px;
    }

    @media (max-width: 1000px) {
        padding: 0 77px;
        padding-top: 11px;
        &--no-margin {
            padding: 0 52px;
            padding-top: 11px;
        }
    }

    @media (max-width: 680px) {
        padding: 11px 20px;
    }
}

.container-relative {
    width: 100%;
    height: 100%;
    position: relative;

    &--z-index {
        z-index: 1;
    }
}

.absolutX-center {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
}

.abolusteY-center {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
}

.for-desc-block {
    display: block;

    @media(max-width: 1000px) {
        display: none !important;
    }
}

.for-mob-block {
    display: block;

    @media(min-width: 1001px) {
        display: none !important;
    }
}

.for-desc-flex {
    display: flex;

    @media(max-width: 1000px) {
        display: none !important;
    }
}

.for-mob-flex {
    display: flex;

    @media(min-width: 1001px) {
        display: none !important;
    }
}

.width-1000-1300 {
    display: none;
    @media(min-width: 1001px) and (max-width: 1300px) {
        display: block;
    }
}
.width-1300-more {
    display: block;
    @media(min-width: 1001px) and (max-width: 1300px) {
        display: none;
    }
}

.overflow-hidden {
    overflow: hidden;
}

.overflow-x-hidden {
    overflow-x: hidden;
}
.overflow-y-hidden {
    overflow-y: hidden;
}
.shadow {
    filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.6));
}
